.btn-reg {
  background-color: #c6211f;
  color: #fff;
  padding: 10px 20px;
  border-radius: 50px;
  font-weight: 600;
  position: relative;
  -webkit-transition: background-color 0.25s;
  -o-transition: background-color 0.25s;
  transition: background-color 0.25s;
}

.btn-reg:hover {
  color: #fff;
  text-decoration: none;
  background-color: #f04b49;
}

.btn-reg:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 4px solid #c6211f;
  z-index: 1;
  border-radius: inherit;
  opacity: 0;
  -webkit-transform: scale3d(0.6, 0.6, 1);
  -moz-transform: scale3d(0.6, 0.6, 1);
  -ms-transform: scale3d(0.6, 0.6, 1);
  -o-transform: scale3d(0.6, 0.6, 1);
  transform: scale3d(0.6, 0.6, 1);
  -webkit-transition: -webkit-transform 0.25s, opacity 0.25s ease;
  -moz-transition: -moz-transform 0.25s, opacity 0.25s ease;
  -o-transition: -o-transform 0.25s, opacity 0.25s ease;
  transition: transform 0.25s, opacity 0.25s ease;
}

.btn-reg:hover:before {
  -webkit-transform: scale3d(1.04, 1.15, 1);
  -moz-transform: scale3d(1.04, 1.15, 1);
  -ms-transform: scale3d(1.04, 1.15, 1);
  -o-transform: scale3d(1.04, 1.15, 1);
  transform: scale3d(1.04, 1.15, 1);
  opacity: 1;
}

.btn-reg:focus {
  text-decoration: none;
  color: #fff;
}

.btn-white {
  background-color: transparent;
